.body-background {
  height: 100%;
  width: 100%;
  z-index: 9;
  background-color: #3f3f3f;
  position: fixed;
  top: 0;
  left: 0;
}

.cat-img {
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

.cat-desc {
  font-size: 24px;
}

.breed-select {
  max-width: 600px;
  font-size: 26px;
}

.loader {
  z-index: 99;
  position: fixed;
  top: 45%;
  left: 42%;
}

.loader__anim {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

